.tile-small{
    width: 50px;
    height: 50px;
}

.tiles-grid {
    position: relative;
    display: grid;
    grid-template-columns: repeat(auto-fit,50px);
    grid-template-rows: repeat(auto-fit,50px);
    grid-gap: 4px;
}

.content {
    border: 1px dashed #887982;
    padding: 10px;
    margin: 10px;
    background-color:#d6cdcdc4;
}

.right-align {
    float: right;
    text-align: right;
}